export default {
	data() {
		return {
			newfloter: {
				// 添加新的花托
				name: '白百何',
				apikey: 'N4Hi4scsutQWHiUhVf=y10S6zj4=',
				ID: 577429787,
				parameter: 'Weight_Shiwu'
			},
			flowerList: [
				//花托列表
				{
					name: '白百何',
					apikey: 'N4Hi4scsutQWHiUhVf=y10S6zj4=',
					ID: 577429787,
					init: 60, // 初始重量
					newvalue: 99, // 实时重量
					water: 40 // 最近一次浇水的量
				}
			],
			isdarwer: false
		};
	},
	onLoad() {},
	methods: {
		ss() {
			console.log('...');
		},
		darwer() {
			this.isdarwer = !this.isdarwer;
		},
		submit(index,flower){
			uni.request({
				url: "https://api.heclouds.com/devices/" + flower.ID + "/datapoints",
				method: 'GET',
				header:{
					'api-key':flower.apikey
				},
				data: {
					limit:10,
					parameter:flower.parameter
				},
				success: res => {
					console.log(res)
				},
				fail: (err) => {
					console.log(err)
				},
				complete: () => {}
			});
		}
	}
};
